import { combineReducers } from 'redux';
import { ADD_TICKET } from '../actions/actionTypes';

const initialState = {
    tickets: [
        'fuck'
    ]
};

function tickets(state = initialState.tickets, action) {
    switch (action.type) {
        case ADD_TICKET:
            return Object.assign({}, state, {
                tickets: [
                    ...state,
                    input
                ]
            });
        default:
            return state;
    }
}

const estimationApp = combineReducers({
    tickets,
});

export default estimationApp;
